
import $ from "../../../static/jquery.js";
import Swiper from "../../../static/swiper.js";
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
name: "app",
components: {
XHeader,
ViewBox,
Flexbox,
FlexboxItem,
Loading
},
mounted() {
if(this.$route.params["order_number"] == "1") {

this.isActive1 = true;
this.isActive = false;
};
if(this.$route.params["order_number"] == "2") {

this.isActive2 = true;
this.isActive = false;
};
if(this.$route.params["order_number"] == "3") {

this.isActive3 = true;
this.isActive = false;
};
if(this.$route.params["order_number"] == "4") {

this.isActive4 = true;
this.isActive = false;
};


this.$get(
"order/getList", {},
res => {
this.order_list = res.msg;
console.log(this.order_list);
}
);


var galleryTop = new Swiper(".gallery-top", {
spaceBetween: 10,
onTransitionStart: function(swiper) {
var id = swiper.activeIndex;
console.log(id);
$("#topNav  .active").removeClass("active");
$("[data-index='" + (id + 1) + "']").addClass("active");
mySwiper.slideTo(id - 1, 500);
}
});
var mySwiper = new Swiper("#topNav", {
freeMode: true,
freeModeMomentumRatio: 0.5,
slidesPerView: "auto"
});
var swiperWidth = mySwiper.container[0].clientWidth;
var maxTranslate = mySwiper.maxTranslate();
var maxWidth = -maxTranslate + swiperWidth / 2;
$(".swiper-container").on("touchstart", function(e) {

});
$("#topNav .swiper-slide").on("click", function(e) {

var id = $(this).attr("data-index");
galleryTop.slideTo(id - 1, 0);
});
mySwiper.on("tap", function(swiper, e) {

var slide = swiper.slides[swiper.clickedIndex];
var slideLeft = slide.offsetLeft;
var slideWidth = slide.clientWidth;
var slideCenter = slideLeft + slideWidth / 2;


mySwiper.setWrapperTransition(300);

if(slideCenter < swiperWidth / 2) {
mySwiper.setWrapperTranslate(0);
} else if(slideCenter > maxWidth) {
mySwiper.setWrapperTranslate(maxTranslate);
} else {
var nowTlanslate = slideCenter - swiperWidth / 2;

mySwiper.setWrapperTranslate(-nowTlanslate);
}

$("#topNav  .active").removeClass("active");

$("#topNav .swiper-slide")
.eq(swiper.clickedIndex)
.addClass("active");
});
},
methods: {

toOrderInfo(group) {
var order_id = group.order_id
console.log("uuu", group)
this.$router.push({
path: "/orderInfo/orderInfo",
name: "/orderInfo/orderInfo",
params: {
order_id: order_id
}
});
},
},
data() {
return {
isActive: true,
isActive1: false,
isActive2: false,
isActive3: false,
isActive4: false,
show: false,
order_list: [],
two: [],
order_state: '',
state: [{
state: '0',
title: "",
type: "info"
},
{
state: '1',
title: "",
type: "warning"
},
{
state: '2',
title: "",
type: ""
},
{
state: '3',
title: "",
type: "success"
},
{
state: '4',
title: "/",
type: "danger"
}
]
};
}
};



